:host {
  @include reset-host;
  --bar-height: 56px;
}

:host .logo-bar {
  display: flex;
  justify-content: end;
  width: 100%;
  height: rem(56);
  overflow: hidden;
  background: linear-gradient(90deg, transparent, $gray-200);
}

:host .gradient {
  flex: 1;
  height: 100%;
}

:host .logo-box {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  width: fit-content;
  max-width: 75vw;
  height: 100%;
  padding: 0 rem(50);
  background-color: $black;

  &:before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    height: 100%;
    content: '';
    border-bottom: rem(56) solid $black;
    border-left: rem(16) solid transparent;
    transform: translateX(-100%);
  }
}

:host .logo {
  display: flex;
  align-items: center;
  width: fit-content;
  height: rem(27);

  @include mq('min', 'md') {
    height: rem(32);
  }
}
