import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString, isEmpty } from '@springbokagency/stencil-base';
import type { BsImage } from '../../atoms/image/image';

/**
 * Share banner component.
 */
@Component({
  tag: 'bs-logo-bar',
  styleUrl: 'logo-bar.scss',
  shadow: true,
})
export class BsLogoBar {
  /**
   * The logo to be displayed .
   */
  @Prop() logo?: BsImage | string;

  @Memoize('logo')
  get parsedLogo(): BsImage {
    return parseIfString(this.logo);
  }

  render() {
    return (
      <Host>
        {!isEmpty(this.parsedLogo.src) && (
          <div class="logo-bar">
            <div class="gradient"></div>
            <div class="logo-box">
              <bs-image
                class="logo"
                src={this.parsedLogo.src}
                altText={this.parsedLogo.altText}
                width={this.parsedLogo.width}
                height={this.parsedLogo.height}
              />
            </div>
          </div>
        )}
      </Host>
    );
  }
}
